@import "normalize";
@import "variables";
@import "mixins";

* {
  box-sizing: border-box;
}


html {
  height: 100%;
  font-size: 10px;
}

body {
  min-height: 100%;
  @include font(1.4rem, $white, 400, $text-ff, 1.6);
  background-color: #f5f6f6;
}

.container {
  max-width: 1320px;
  width: 95%;
  margin: 0 auto;
}

.header {
  position: fixed;
  z-index: 10;
  padding: 20px 0px;
  width: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0));
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__log {
    flex-basis: 20%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.logo {
  text-decoration: none;
  text-transform: uppercase;
  color: $white;
  font-family: $logo-ff;
  font-size: 3.5rem;
  outline: none;

  &--footer {
    color: $brand-color;
  }

}

.btn {
  outline: none;
  border: none;
  background: none;
  border-radius: 5px;
  font-size: 1.6rem;
  color: $white;
  font-weight: 400;
  cursor: pointer;
  padding: 8px 25px;
  border: $b-size solid transparent;

  &--search {
    margin: 0;
    padding: 0;
  }

  &--simple {
    padding: 10px;
    // margin: 0 40px;
  }

  &--bg {
    background-color: $brand-color;
  }

  &--bordered {
    border-color: $white;
  }
}

.slider {
  height: 100vh;
  position: relative;

  &__slide {
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;

    &::after {
      content: '';
      width: 100%;
      height: 100%;
      display: block;
      position: absolute;
      z-index: -9;
      background: linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0) 100%);
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    z-index: -10;
  }

  &__wrap {
    display: flex;
  }

  &__block {
    flex-basis: 50%;
  }

  &__info {
    padding: 50px 0;
    max-width: 1320px;
    width: 95%;
    margin: 0 auto;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }

  &__title {
    font-weight: 700;
    text-transform: uppercase;
    font-size: 4rem;
    display: inline-block;
    margin: 0;
    line-height: 1.3;
  }

  &__genre {
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-between;
    max-width: 370px;
    font-size: 1.6rem;
  }

  &__genre-item {
    list-style: none;
    position: relative;

    &--before {
      margin-left: 20px;

      &:before {
        content: '';
        height: 17px;
        width: $b-size;
        display: block;
        background-color: $white;
        position: absolute;
        top: 50%;
        left: -35%;
        transform: translate(0, -50%);
      }
    }
  }

  &__btns {
    flex-basis: 34%;
    flex-wrap: wrap;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.rating {
  margin-top: 30px;
  width: 100%;
  display: flex;
  align-items: center;

  &-num {
    font-size: 1.6rem;
    color: $white;
    border: 1.5px solid $brand-color;
    border-radius: 5px;
    line-height: 1;
    padding: 3px 7px;
    margin-left: 10px;

    &--brand {
      color: $brand-color;
    }
  }
}

.swiper-pagination {
  position: absolute;
  right: 100px !important;

}

.swiper-pagination-bullet {
  width: 5px;
  height: 25px;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0.5);
  opacity: 1;
  margin: 15px 0 !important;
}

.swiper-pagination-bullet-active {
  background-color: $brand-color;
}

:root {
  --star-size: 30px;
  --star-color: #b3b3b3;
  --star-background: #47c3f0;
}

.Stars {
  --percent: calc(var(--rating) / 5 * 100%);
  display: inline-block;
  font-size: var(--star-size);
  font-family: Times; // make sure ★ appears correctly
  line-height: 1;

  &::before {
    content: '★★★★★';
    letter-spacing: 3px;
    background: linear-gradient(90deg, var(--star-background) var(--percent), var(--star-color) var(--percent));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

.movies {
  // height: 100vh;

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding: 35px 0 0 0;
  }

  &__tabs {
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-between;
  }

  &__tab {

    list-style: none;
    color: $brand-color;
    margin-right: 30px;
    font-size: 1.6rem;
    border-bottom: $b-size solid transparent;
    cursor: pointer;
    transition: all 0.25s ease;
    padding-bottom: 5px;

    @include hover {
      border-bottom: $b-size solid $brand-color;
    }

    &--genres {
      position: relative;
      display: block;

      i {

        font-size: 2rem;
        line-height: 1;
      }
    }
  }

  &__genres {
    position: absolute;
    display: none;
    top: 115%;
    z-index: 10;
    list-style: none;
    padding: 0;
    background-color: #f5f6f6;

    & li {
      padding: 5px 15px;
    }

  }

  &__view {
    &-svg {
      margin-left: 15px;
      fill: #d6d8d8;
      opacity: 0.8;
      cursor: pointer;
    }
  }

  &__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 30px;
  }

  &__item {
    // border: 1px solid red;
    width: 300px;
    height: 370px;
    border-radius: 5px;
    margin-bottom: 40px;
    overflow: hidden;
    background-color: transparent;
    box-shadow: 0 5px 30px 0 rgba(0, 0, 0, 0.04);

  }

  &__main {
    background-color: $white;
    height: 100%;
    cursor: pointer;

    &-img-wrap {
      position: relative;
      width: 300px;
      height: 270px;
    }

    &-img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      object-fit: cover;

      @include hover {
        transform: translate(-50%, -50%) scale(1.05);
      }
    }

    &-info {
      padding: 25px;
      display: flex;
      justify-content: space-between;
      align-items: flex-start
    }

    &-text {
      max-width: 185px;
    }

    &-title {
      display: block;
      color: $black;
      font-size: 1.7rem;
      text-transform: uppercase;
      font-weight: 700;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    &-genre {
      color: $brand-color;
      display: block;
      font-size: 1.5rem;
      font-weight: 500;
      line-height: 1.2;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  &__secondary {
    width: 100%;
    height: 100%;
    position: relative;
    cursor: pointer;

    &-img {
      position: absolute;
      @include pCenter;
      width: 100%;
      height: 100%;
      object-fit: cover;
      // z-index: 0;
    }

    &-info {
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.65);
      // z-index: 2;
      opacity: 0.99;
      padding: 25px;

      .btn--simple {
        padding: 0;
        line-height: 1;

        &:nth-child(1) {
          margin-right: 15px;
        }
      }
    }

    &-btns {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-btns-right {
      display: flex;
      align-items: center;
    }

    &-titleblock {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      margin-top: 20px;

      .movies__main-title {
        color: $white;
      }
    }

    &-description {
      margin: 20px 0;
      max-height: 112px;
      overflow: hidden;
    }
  }

}

.footer {
  background-color: $white;
  box-shadow: 0 5px 30px 0 rgba(0, 0, 0, 0.04);


  &__wrap {
    display: flex;
    justify-content: space-between;
    padding: 40px 0;
  }

  &__nav {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-between;

    &-item {
      margin-right: 20px;
    }
  }

  &__link {
    text-decoration: none;
    color: $muted-color;

    @include hover {
      color: $brand-color;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__copyright {
    margin-top: 20px;
    color: $muted-color;
    font-family: 'Times New Roman', Times, serif;
    letter-spacing: 0.6px;

    &-logo {
      font-family: $logo-ff;
    }

  }

  &__socials {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 2.5rem;
    display: flex;
    justify-content: space-between;
  }

  &__social {
    margin-left: 15px;
  }
}

.active-svg {
  fill: $brand-color;
}

.active-tab {
  font-weight: 500;
  border-bottom: $b-size solid $brand-color;
}

.close {
  fill: $white;
}

.disabled {
  display: none;
}

.fade {
  -webkit-animation-name: fade;
  animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-duration: 1.5s;
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
}

